import { Injectable, EventEmitter} from '@angular/core';
import { Recipe} from "./recipe";
import { Ingredient } from './ingredient';
import { Headers,Http, Response } from "@angular/http"
import 'rxjs/Rx';

@Injectable()
export class RecipeService {
  recipesChanged = new EventEmitter<Recipe[]>();
  private recipes:Recipe[]=[
    new Recipe('French Fries', 'Very tasty', 'http://www.texaschickenmalaysia.com/menu/sides-french-fries.png', [
      new Ingredient('Potatoes', 2),
      new Ingredient('Oil', 1)
    ]),
    new Recipe('Summer Salad', 'Okayish', 'http://ohmyveggies.com/wp-content/uploads/2013/06/the_perfect_summer_salad.jpg', [])
  ];
  constructor(private http:Http) { }

  getRecipes(){
    return this.recipes;
  }
  getRecipe(id:number){
    return this.recipes[id];
  }

  deleteRecipe(recipe:Recipe){
    this.recipes.splice(this.recipes.indexOf(recipe),1);
  }

  addRecipe(recipe: Recipe){
    this.recipes.push(recipe);
  }
  editRecipe(oldRecipe: Recipe, newRecipe: Recipe){
    this.recipes[this.recipes.indexOf(oldRecipe)] = newRecipe;
  }

  storeData(){
    const body = JSON.stringify(this.recipes);
    const headers = new Headers({
      'Content-Type': 'application/json'
    }
    );
    return this.http.put('https://recipebook-1d42c.firebaseio.com/recipes.json',body,
    {headers: headers}
    ); //created observable above
  }

    fetchData() {
    return this.http.get('https://recipebook-1d42c.firebaseio.com/recipes.json')
      .map((response: Response) => response.json())
      .subscribe(
        (data: Recipe[]) => {
          this.recipes = data;
          this.recipesChanged.emit(this.recipes);
        }
      );
  }
  
}



